@import "../modules/reset.scss";
@import '../layui/index.scss';
body{
	background:#364150;
}
.login{
	.logo{
		margin: 60px auto 0;
		padding: 15px;
		text-align: center
	}


}
.content{
	background-color: #eceef1;
	-webkit-border-radius: 7px;
	border-radius: 7px;
	width: 400px;
	margin: 40px auto 10px;
	padding: 10px 30px 30px;
	overflow: hidden;
	position: relative;
	.form-group{
		margin-bottom:15px;
	}
}

.login-title{
	margin-bottom:25px;
	text-align:center;
	font-size:28px;
	font-weight:400;
	color:#32c5d2;
}
.login-user{
	background: #dde3ec;
	width:100%;
	height:43px;
	color:#8290a3;
	border:1px solid #dde3ec;
	padding:6px 12px;
}
.user-msg{
	position:relative;
}
.user-verify{
	position: absolute;
	right:0;
	top:0;
	height:43px;
	width: 100px;
	cursor: pointer;
	background:#fff;
}

.user-submit{
	padding:10px 20px;
	font-weight:600;
	color:#fff;
	background:#32c5d2;
	height:42px;
	line-height:22px;
	width:100%;
	margin-top:30px;
}
